import request from '../request.js'

export function get(api, params) {
  return request.get(api, params);
}

export function post(api, params) {
  return request.post(api, params);
}


const baseUrl = "http://localhost:8080";
const getKlineByCodeUrl = "/EastMoney/getKlineByCode";


export function getKlineByCode(code) {
  return request.get(getKlineByCodeUrl, {code: code});
}




